import { ThunkDispatch } from "redux-thunk";
import {
  SAVE_COURSES,
  FAILURE_COURSE,
  REQUEST_COURSE,
  SUCCESS_COURSE,
} from "../_types/course.types";
import { AppState } from "../_types";
import { API } from "../_helpers/api";
import { AppActions } from "../_types";
import { requestGuide } from "./guide.actions";
import { requestLesson } from "./lesson.actions";
import { PathFinder } from "../_helpers/constants";
import { requestExercise } from "./exercise.actions";
import { SAVE_EXERCISES } from "../_types/exercise.types";
import { ILesson, SAVE_LESSONS } from "../_types/lesson.types";
import { errorHandler } from "../components/Layout/SnackBar/alert";

export const requestCourse = (path: PathFinder) => (
  dispatch: ThunkDispatch<AppState, undefined, AppActions>,
  getState: () => AppState
) => {
  const found = getState().bank.courses.data.find(
    (course) => course.id === path.course
  );
  const { course, ...newPath } = path;
  if (found) {
    const compiler = Compiler(found.course_select);
    dispatch({ type: SUCCESS_COURSE, data: found });
    dispatch({ type: SAVE_LESSONS, data: compiler.lessons });
    dispatch({ type: SAVE_EXERCISES, data: compiler.exercises });
    if (path.lesson) dispatch(requestLesson(newPath));
    if (path.exercise) dispatch(requestExercise(newPath));
  } else if (path.guide) {
    dispatch(requestGuide(path));
  } else if (!found) {
    dispatch({ type: REQUEST_COURSE });
    API.get(`/lesson/v2/course?id=${path.course}`)
      .then((res) => {
        const compiler = Compiler(res.data[0].course_select);
        dispatch({ type: SUCCESS_COURSE, data: res.data[0] });
        dispatch({ type: SAVE_COURSES, data: res.data });
        dispatch({ type: SAVE_LESSONS, data: compiler.lessons });
        dispatch({ type: SAVE_EXERCISES, data: compiler.exercises });
        if (path.lesson) dispatch(requestLesson(newPath));
        if (path.exercise) dispatch(requestExercise(newPath));
      })
      .catch((err) => errorHandler(err, FAILURE_COURSE));
  }
};

const Compiler = (course_select: ILesson[]) => {
  const lessons: ILesson[] = [];
  const exercises: ILesson[] = [];
  course_select.map((select) =>
    select.info.genre.name === "Exercise"
      ? exercises.push(select)
      : lessons.push(select)
  );
  return { lessons, exercises };
};
