import { ThunkDispatch } from "redux-thunk";
import {
  FAILURE_EXERCISE,
  REQUEST_EXERCISE,
  SUCCESS_EXERCISE,
  SAVE_EXERCISES,
} from "../_types/exercise.types";
import { AppState } from "../_types";
import { API } from "../_helpers/api";
import { AppActions } from "../_types";
import { requestGuide } from "./guide.actions";
import { requestCourse } from "./course.actions";
import { PathFinder } from "../_helpers/constants";
import { ENTER_NOTEMODE } from "../_types/player.types";
import { errorHandler } from "../components/Layout/SnackBar/alert";

export const requestExercise = (path: PathFinder) => (
  dispatch: ThunkDispatch<AppState, undefined, AppActions>,
  getState: () => AppState
) => {
  const found = getState().bank.exercises.data.find(
    (exercise) => exercise.id === path.exercise
  );
  if (found) {
    dispatch({ type: ENTER_NOTEMODE });
    dispatch({ type: SUCCESS_EXERCISE, data: found });
  } else if (path.guide) {
    dispatch(requestGuide(path));
  } else if (path.course) {
    dispatch(requestCourse(path));
  } else if (!found) {
    dispatch({ type: REQUEST_EXERCISE });
    API.get(`/lesson/v2?id=${path.exercise}`)
      .then((res) => {
        dispatch({ type: ENTER_NOTEMODE });
        dispatch({ type: SUCCESS_EXERCISE, data: res.data[0] });
        dispatch({ type: SAVE_EXERCISES, data: res.data });
      })
      .catch((err) => errorHandler(err, FAILURE_EXERCISE));
  }
};
